@tailwind base;
@tailwind components;
@tailwind utilities;

.btn {
  background: linear-gradient(
    92.23deg,
    
    #410909 1.63%,
    #972020 100%,
    #5d0202 117.04%
  );
  box-shadow: 0px 4.42184px 107.23px rgba(255, 86, 246, 0.51);
}

.btn:hover {
  background: linear-gradient(
    92.23deg,
    #ff4f4f 21.43%,
    #f22c2c 50.63%,
    #7a2020 100%,
    #e11818 117.04%
  );
}

.gradient,
.active {
  background: linear-gradient(
    92.23deg,
    #410909 1.63%,
    #972020 100%,
    #5d0202 117.04%
  );
}

.active {
  color: #fff;
  padding: 1rem;
  border-radius: 100%;
}

@layer base {
  body {
    @apply font-secondary text-lg leading-8 text-white;
  }
  .h2 {
    @apply font-primary text-[32px] mb-6 tracking-[10%] uppercase;
  }
  .h3 {
    @apply font-primary font-semibold text-[26px] mb-6 leading-[46px];
  }
  .btn {
    @apply rounded-full font-primary text-white font-medium;
  }
  .btn-sm {
    @apply h-[48px] px-6 text-sm;
  }
  .btn-lg {
    @apply h-[56px] px-10 text-base;
  }
  .btn-link {
    @apply text-base;
  }
  .text-gradient {
    @apply bg-gradient-to-r text-transparent bg-clip-text from-[#e11818] to-[#972020] font-bold font-primary hover:from-[#5d0202] hover:to-[#ff4f4f];
  }
  .section {
    @apply py-8 lg:py-24 lg:h-screen flex items-center;
  }
}
